","sourcesContent":["/*===== GOOGLE FONTS =====*/\r\n@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&family=Montserrat:wght@400;500&display=swap');\r\n\r\n/*===== VARIABLES CSS =====*/\r\n\r\n\r\n$header-height: 100px;\r\n\r\n$base-font-size: 40px;\r\n/*========== Colors ==========*/\r\n$first-color: #4FB2E5;\r\n$second-color: rgba(79, 178, 229, 0.2);\r\n$title-color: #2F6E89;\r\n$text-color: #4B4B4B;\r\n$text-color-light: #FFFFFF;\r\n\r\n/*========== Font and typography ==========*/\r\n$body-font: 'Lato', sans-serif;\r\n$biggest-font-size: 56px;\r\n$h2-font-size: 32px;\r\n$h3-font-size: 17px;\r\n$normal-font-size: 16px;\r\n$font-semi-bold: 600;\r\n$font-bold: 700;\r\n$font-black: 900;\r\n$font-regular: 400;\r\n$z-fixed: 100;\r\n/*========== BASE ==========*/\r\n*, ::before, ::after {\r\n  box-sizing: border-box;\r\n  margin: 0;\r\n  padding: 0;\r\n}\r\n\r\nhtml {\r\n  scroll-behavior: smooth;\r\n  transition: 3s;\r\n}\r\n\r\n\r\nbody {\r\n  margin: 0;\r\n  font-family: $body-font;\r\n  font-size: $normal-font-size;\r\n  color: $text-color;\r\n}\r\n\r\nul {\r\n  padding: 0;\r\n  list-style: none;\r\n}\r\n\r\na {\r\n  text-decoration: none;\r\n  color: inherit;\r\n}\r\n\r\nimg {\r\n  max-width: 100%;\r\n  height: auto;\r\n}\r\n\r\nh1 {\r\n  font-weight: $font-black;\r\n  font-size: $biggest-font-size;\r\n  color: $title-color;\r\n  line-height: 73px;\r\n}\r\n\r\nh2 {\r\n  font-weight: $font-bold;\r\n  font-size: $h2-font-size;\r\n  color: $title-color;\r\n  text-align: center;\r\n}\r\n\r\nh3 {\r\n  font-weight: $font-regular;\r\n  font-size: $h3-font-size;\r\n  color: $title-color;\r\n  line-height: 20px;\r\n  letter-spacing: 1px;\r\n}\r\n\r\np {\r\n  font-weight: 300;\r\n  font-size: $normal-font-size;\r\n  line-height: 28px;\r\n  color: $text-color;\r\n}\r\n\r\nh1, h2, h3, ul, p {\r\n  margin: 0;\r\n}\r\n\r\n//###################################### HEADER ################################\r\n.main-header {\r\n  position: fixed;\r\n  z-index: 5;\r\n  width: 100%;\r\n  padding: 30px 0;\r\n  transition: 1s;\r\n}\r\n\r\n.sticky {\r\n  background: #fff;\r\n  padding: 10px 0;\r\n  box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.1);\r\n\r\n}\r\n\r\n//###################################### NAV ####################################\r\n\r\nnav.main-nav {\r\n  max-width: 90%;\r\n  margin: auto;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: space-between;\r\n\r\n  .nav__menu-list {\r\n    display: inline-flex;\r\n    align-items: center;\r\n\r\n    li.nav__item.reservation a {\r\n      color: white;\r\n      line-height: 17px;\r\n      text-transform: uppercase;\r\n    }\r\n\r\n    .nav__item {\r\n\r\n\r\n      margin-left: 25px;\r\n\r\n      & a {\r\n        transition: all .5s ease;\r\n        text-transform: uppercase;\r\n        color: $text-color;\r\n        font-size: calc(13px + .2vw);\r\n        font-weight: 500;\r\n\r\n      }\r\n\r\n      & a:hover {\r\n        color: $first-color;\r\n        font-weight: bolder;\r\n      }\r\n\r\n      &.reservation {\r\n\r\n        display: block;\r\n        letter-spacing: 0.5px;\r\n        border-radius: 5px;\r\n        padding: 10px 9px;\r\n        border: 1px solid white;\r\n        margin-left: calc(20px + 8vw);\r\n        background-color: $first-color;\r\n      }\r\n\r\n    }\r\n\r\n  }\r\n}\r\n\r\nion-icon {\r\n  color: $text-color;\r\n  font-size: 30px;\r\n  cursor: pointer;\r\n  --ionicon-stroke-width: 60px;\r\n  display: none;\r\n}\r\n\r\n.menu-close {\r\n  position: absolute;\r\n  top: 20px;\r\n  right: 30px;\r\n}\r\n\r\n\r\n//######################################## MAIN #############################################\r\n//###### ############################REUSABLE #########################################\r\n.section__description {\r\n  font-weight: normal;\r\n  font-size: calc(10px + .5vw);\r\n  line-height: 1.5;\r\n  text-transform: uppercase;\r\n  letter-spacing: 0.06vw;\r\n  color: $title-color;\r\n\r\n\r\n}\r\n\r\n//###### ####